import React from 'react'
import GoogleMapReact from 'google-map-react'
import {Paper,Typography,useMediaQuery} from '@material-ui/core'
import LocationOnOutLinedIcon from '@material-ui/icons/LocationOnOutlined'
import Rating from '@material-ui/lab/Rating'
import useStyles from'./styles'
const Map = ({setCoordinates,setBounds,coordinates,places}) => {
    const classes=useStyles()
    const isMobile=useMediaQuery('(min-width:600px)')
  return (
    <div className={classes.mapContainer}>
        <GoogleMapReact
        bootstrapURLKeys={{key:process.env.REACT_APP_GOOGLE_MAPS_API_KEY}}
        defaultCenter={coordinates}
        center={coordinates}
        defaultZoom={14}
        options={''}
        onChange={(e)=>{setCoordinates({lng:e.center.lng, lat:e.center.lat})
      setBounds({sw:e.marginBounds.sw ,ne:e.marginBounds.ne})}}
        >
          {places?.map((place,i)=>(
            <div className={classes.markerContainer} key={i} lat={Number(place.latitude)}
            lng={Number(place.longitude)}>
             <Paper elevation={3} className={classes.paper}>
                  <Typography className={classes.typography} variant="subtitle2" gutterBotttom>{place.name}</Typography>
                  <img className={classes.pointer}
                  src={place.photo?place.photo.images.large.url:'https://www.foodserviceandhospitality.com/wp-content/uploads/2016/09/Restaurant-Placeholder-001.jpg'}/>
                  <Rating size="small"value={Number(place.rating)}readOnly/>
                </Paper>
            </div>
          ))}
        </GoogleMapReact>
    </div>
  )
}

export default Map